.elg-banner__slide {
  width: 100%;
  height: 600px;
  position: relative;
  /*  h tr responsive, cn media query */
  animation-delay: 0.45s;
  animation-duration: 0.9s, 0.7s;
  animation-timing-function: cubic-bezier(0.455, 0.03, 0.515, 0.955);
  animation-fill-mode: both;
  clip-path: inset(0%);
  /* Responsive (md: 768px) */
}
.elg-banner__slide-bg {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
}
.elg-banner__slide-content {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  gap: 2.5rem; /* 10 * 0.25rem */
  height: 100%;
  position: relative;
  z-index: 10;
}
.elg-banner__slide-content > * {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding-top: 5rem; /* 20 * 0.25rem */
  padding-bottom: 5rem; /* 20 * 0.25rem */
  max-width: 440px;
  color: var(--color-cake-neutral-900); /* m bo bn  nh ngha bin mu ny */
  /* Responsive: md: 768px */
}
@media (max-width: 768px) {
  .elg-banner__slide-content > * {
    padding-top: 2.5rem; /* 10 * 0.25rem */
    padding-bottom: 0;
    max-width: 100%;
  }
}
.elg-banner__slide-title {
  font-weight: 600; /* font-semibold */
  font-size: 3rem; /* text-5xl: 48px (default Tailwind) */
  text-align: left; /* text-left */
  overflow-wrap: break-word; /* break-words */
  margin-bottom: 1rem; /* mb-4 */
  display: -webkit-box; /* line-clamp cn */
  -webkit-line-clamp: 3; /* line-clamp-3 */
  -webkit-box-orient: vertical;
  overflow: hidden;
  line-height: 60px; /* leading-[60px] */
  color: white;
}
.elg-banner__slide-description {
  margin-bottom: 2rem;
  font-weight: 400;
  font-size: 1rem;
  text-align: left;
  overflow-wrap: break-word;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  color: white;
}
@media (max-width: 768px) {
  .elg-banner__slide-content {
    flex-direction: column;
    gap: 1rem; /* 4 * 0.25rem */
  }
  .elg-banner__slide-title {
    font-size: 1.5rem;
    line-height: 2.5rem;
  }
  .elg-banner__slide-description {
    margin-bottom: 1rem;
  }
}
@media (max-width: 768px) {
  .elg-banner__slide {
    height: 560px;
  }
}
@media (max-width: 1024px) {
  .elg-banner__slide {
    clip-path: none !important;
  }
}
